<template>
  <v-form @submit.prevent="submit" v-model="valid" class="mx-auto register-form">
    <h2 class="text-center">忘記密碼</h2>
      <p class="text-center mt-5">請輸入註冊信箱，將寄送驗證碼至您的信箱</p>
    <v-text-field hide-details="auto" v-model="form.email" density="compact" label="申請原因"
      prepend-inner-icon="mdi-email" variant="solo" required class="register-item mb-2" :rules="[emailRule]">
      <template #label>
        <span class="text-danger">*</span>帳號/信箱
      </template>
    </v-text-field>

    <v-btn type="submit" variant="flat" color="primary" block class="mt-5" :loading="loading">
      發送
    </v-btn>

    <div class="or">或</div>
    <router-link to="/sign-in" class="text-decoration-none">
      回登入
    </router-link>
  </v-form>
</template>

<script setup>
import { authentication } from "@/requests";
import { useReCaptcha } from "vue-recaptcha-v3";
import { alertError } from "@/helpers/functions";
import { emailRule } from "@/rules";

const router = useRouter();
const { executeRecaptcha } = useReCaptcha();

const loading = ref(false);
const valid = ref(true);

const form = ref({
  email: "",
});

const submit = async () => {
  const rules = [emailRule];
  for (const rule of rules) {
    const result = rule(form.value[Object.keys(form.value).find(key => rule === eval(key + 'Rule'))]);
    if (result !== true) {
      alert(result);
      return;
    }
  }
  loading.value = true;
  router.push({ path: "/sign-in/forgetSet", query: { email: form.value.email } });
};
</script>
<style scoped>
.register-form {
  min-width: 60%;
}

.register-item {
  border-radius: 10px !important;
  margin-top: 18px;
}

.register-item .v-field {
  height: 25px;
}
.register-item .v-input__control {
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.5) !important;
  background-color: white;
}
:global(.right .register-item .v-input__details .v-messages__message) {
  color: lightcoral !important;
}

:global(.register-item .v-field) {
  border-radius: 10px !important;
}

:global(.right .register-item .v-input__control *) {
  color: gray !important;
}

:global(.right .register-item .text-danger) {
  color: red !important;
}

.label-container {
  display: flex;
  justify-content: space-between;
  min-width: 280px;
}
.label-item {
  flex: 1;
  min-width: 100px;
}
</style>
